<template>
  <div>
    <el-input type="textarea"
              :rows="4"
              :autosize="{ minRows: 4, maxRows: 4}"
              placeholder="请输入字符"
              v-model="text">
    </el-input>
    <el-switch class="val-title"
               v-model="lowerCase"
               active-color="#13ce66"
               inactive-color="#ff4949"
               active-text="小写"
               inactive-text="大写">
    </el-switch>
    <el-row class="line">
      <el-col :span="2">
        <el-button class="val-title"
                   plain
                   round
                   @click="copyValue(md5Value)"
                   type="primary">MD5</el-button>
      </el-col>
      <el-col :span="22">
        <span class="val-class">{{md5Value}}</span>
      </el-col>
    </el-row>
    <el-row class="line">
      <el-col :span="2">
        <el-button class="val-title"
                   type="primary"
                   plain
                   round
                   @click="copyValue(sha1Value)">SHA1</el-button>
      </el-col>
      <el-col :span="22">
        <span class="val-class">{{sha1Value}}</span>
      </el-col>
    </el-row>
    <el-row class="line">
      <el-col :span="2">
        <el-button class="val-title"
                   type="primary"
                   plain
                   round
                   @click="copyValue(sha256Value)">SHA256</el-button>
      </el-col>
      <el-col :span="22">
        <span class="val-class">{{sha256Value}}</span>
      </el-col>
    </el-row>
    <el-row class="line">
      <el-col :span="2">
        <el-button class="val-title"
                   type="primary"
                   plain
                   round
                   @click="copyValue(sha512Value)">SHA512</el-button>
      </el-col>
      <el-col :span="22">
        <span class="val-class">{{sha512Value}}</span>
      </el-col>
    </el-row>
    <el-row class="line">
      <el-col :span="3">
        <el-button class="val-title"
                   type="primary"
                   plain
                   round
                   @click="copyValue(base64Value)">BASE64</el-button>
        <el-switch class="val-title"
                   v-model="base64Encode"
                   active-color="#13ce66"
                   inactive-color="#ff4949"
                   active-text="加密"
                   inactive-text="解密">
        </el-switch>
      </el-col>
      <el-col :span="21">
        <span class="val-class val-base64">{{base64Value}}</span>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import md5 from 'js-md5'
import { Base64 } from 'js-base64'
import sha1 from 'js-sha1'
import sha256 from 'js-sha256'
import sha512 from 'js-sha512'
import copy from 'clipboard-copy';
export default {
  data () {
    return {
      text: '',
      hint: '请输入字符',
      base64Encode: true,
      lowerCase: true
    }
  },
  methods: {
    copyValue (val) {
      if (val != this.hint) {
        copy(val).then(res => {
          this.$message({
            message: '复制成功',
            type: 'success'
          });
        }).catch(err => {
          this.$message({
            message: '复制失败',
            type: 'error'
          });
        })
      }
    },
  },
  computed: {
    md5Value () {
      if (this.text.length === 0) {
        return this.hint
      }
      let val = md5(this.text);
      return this.lowerCase ? val : val.toUpperCase()
    },
    base64Value () {
      if (this.text.length === 0) {
        return this.hint
      }
      return this.base64Encode ? Base64.encode(this.text) : Base64.decode(this.text)
    },
    sha1Value () {
      if (this.text.length === 0) {
        return this.hint
      }
      let val = sha1(this.text)
      return this.lowerCase ? val : val.toUpperCase()
    },
    sha256Value () {
      if (this.text.length === 0) {
        return this.hint
      }
      let val = sha256(this.text)
      return this.lowerCase ? val : val.toUpperCase()
    },
    sha512Value () {
      if (this.text.length === 0) {
        return this.hint
      }
      let val = sha512(this.text)
      return this.lowerCase ? val : val.toUpperCase()
    }
  }
}
</script>
<style scoped>
.val-title {
  color: #606266;
  font-family: monospace;
  text-align: left;
  display: block;
  font-weight: bold;
  font-size: 1.2em;
  padding: 5px 15px 5px 15px;
  margin: 5px 0px 5px 0px;
}
.val-class {
  color: #606266;
  font-family: monospace;
  border: 1px solid #ebeef5;
  display: block;
  height: 18px;
  margin: 5px 0px 5px 0px;
  padding: 5px 15px 5px 15px;
  border-radius: 4px;
  word-break: normal;
  width: auto;
  display: block;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow-y: auto;
}
.val-base64 {
  min-height: 100px;
}
.line {
  margin-top: 5px;
  margin-bottom: 5px;
  padding-top: 2px;
  padding-bottom: 2px;
}
.line-title-col {
  padding: 5px 15px 5px 15px;
}
</style>


