<template>
  <el-container>
    <el-header class="header-class">
      <el-menu :default-active="activeIndex"
               class="el-menu-demo"
               mode="horizontal"
               @select="handleSelect"
               background-color="#36A5E2"
               text-color="#fff"
               active-text-color="#fefefe">
        <el-menu-item class="menu-font"
                      index="Gadget">小玩意</el-menu-item>
        <el-menu-item class="menu-font"
                      index="Cockpit">驾驶舱</el-menu-item>
        <el-menu-item class="menu-font"
                      index="FileBrowser">文件浏览</el-menu-item>
        <el-menu-item class="menu-font"
                      index="QuickCommand">快捷命令</el-menu-item>
      </el-menu>
    </el-header>
    <el-main>
      <el-card class="box-card">
        <router-view />
      </el-card>
    </el-main>
  </el-container>

</template>
<script>
export default {
  data () {
    return {
      activeIndex: 'Gadget',
    };
  },
  created () {
    this.$router.push('/' + this.activeIndex)
  },
  watch: {
    activeIndex () {
      this.$router.push('/' + this.activeIndex)
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath);
      this.activeIndex = key
    }
  }
}
</script>
<style scoped>
.box-card {
  height: 100%;
}
.menu-font {
  font-weight: bold;
}
.header-class {
  padding: 0;
}
.el-menu-demo {
  padding-left: 25px;
  padding-right: 25px;
}
</style>


