<template>
  <el-container>
    <el-header>
      <el-row>
        <el-button v-for="(tool,idx) in items"
                   :key="idx"
                   :type="tool.type"
                   @click="clickToolHandle(tool)"
                   :to="tool.path"
                   round>{{tool.name}}</el-button>
      </el-row>
    </el-header>
    <el-main>
      <el-card class="box-card">
        <router-view />
      </el-card>
    </el-main>
  </el-container>
</template>
<script>
export default {
  data () {
    return {
      items: [
        { type: 'primary', name: '加解密', path: '/Gadget/Codec' },
        { type: 'success', name: 'JSON', path: '/Gadget/JSON' },
        { type: 'info', name: '正则编辑', path: '/Gadget/Codec' },
        { type: 'danger', name: '做选择', path: '/Gadget/Codec' }
      ]
    }
  },
  methods: {
    clickToolHandle (tool) {
      this.$router.push(tool.path)
    }
  }
}
</script>
<style scoped>
.tool-btn {
  margin-left: 10px;
  margin-right: 10px;
}
.box-card {
  height: 100%;
}
</style>


