<template>
  <div>
    <el-input type="textarea"
              :rows="2"
              placeholder="请输入明文"
              v-model="text">
    </el-input>
    <el-row>
      <el-col :span="12"
              class="item-row">
        <span>MD5</span>
        <span class="val-class">{{md5Value}}</span>
      </el-col>
      <el-col :span="12"
              class="item-row">
        <span>SHA1</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24"
              class="item-row">
        <span>BASE64</span>
        <span class="val-class">{{base64Value}}</span>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import md5 from 'js-md5'
import { Base64 } from 'js-base64'
export default {
  data () {
    return {
      text: ''
    }
  },
  computed: {
    md5Value () {
      if (this.text.length === 0) {
        return ''
      }
      return md5(this.text)
    },
    base64Value () {
      if (this.text.length === 0) {
        return ''
      }
      return Base64.encode(this.text)

    }
  }
}
</script>
<style scoped>
.item-row {
  background: #f7fff1;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  font: 1.2em sans-serif;
}
.val-class {
  background: #d5ede1;
  margin-left: 10px;
}
</style>


